@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700&display=swap");
:root {
  --clr-bg1: hsla(248, 35%, 8%, 1);
  --clr-bg2: hsla(234, 32%, 17%, 1);
  --clr-bg3: hsla(244, 16%, 40%, 1);
  --clr-btn-bg: hsla(230, 5%, 45%, 1);
  --clr-btn-active: hsla(236, 80%, 57%, 1);
  --clr-text1: hsla(240, 9%, 91%, 1);
  --clr-text2: hsla(243, 10%, 66%, 1);
  --clr-text3: hsla(245, 11%, 57%, 1);
  --clr-text4: hsla(231, 9%, 42%, 1);
  --clr-in: hsla(53, 100%, 70%, 1);
  --font-w400: 400;
  --font-w500: 500;
  --font-w600: 600;
  --font-w700: 700;
}

*,
*::after,
*::before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-size: 1rem;
  line-height: 1.174em;
  font-family: "Raleway", sans-serif;
}

.container {
  padding: 0 15px;
  margin-right: auto;
  margin-left: auto;
}

/* Medium devices (tablets, 768px and up) The navbar toggle appears at this breakpoint */
@media only screen and (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media only screen and (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
/* Large devices (desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
/* Extra large devices (large desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
.flex {
  --gap: ;
  display: flex;
  gap: var(--gap) 1em;
}

.grid {
  --gap: ;
  display: grid;
  grid-template-rows: auto;
  gap: var(--gap) 1em;
}

