/**
 * Basic typography style for copy text
 */
.days-card {
  --gap: 0em;
  padding: 1em;
  flex-direction: column;
  background-color: var(--clr-bg2);
}
.days-card h3 {
  font-size: 1em;
  font-weight: var(--font-w500);
  color: var(--clr-text1);
  margin-bottom: 0.5em;
}
.days-card .img {
  max-width: 56.44px;
  margin: 0 auto;
}
.days-card .temp {
  margin-top: 3em;
  justify-content: space-around;
}
.days-card .temp__high {
  font-size: 1em;
  font-weight: var(--font-w500);
  color: var(--clr-text1);
}
.days-card .temp__low {
  font-size: 1em;
  font-weight: var(--font-w500);
  color: var(--clr-text2);
}

.hightlights__cards-card {
  padding: 1.5em;
  background-color: var(--clr-bg2);
}
.hightlights__cards-card .title {
  font-size: 1em;
  font-weight: var(--font-w500);
  color: var(--clr-text1);
}
.hightlights__cards-card .text {
  font-size: 4em;
  font-weight: var(--font-w700);
  color: var(--clr-text1);
}
.hightlights__cards-card .text span {
  font-size: 0.5625em;
  font-weight: var(--font-w500);
}
.hightlights__cards-card .arrow {
  --rotate: 0deg;
  max-width: 30px;
  aspect-ratio: 1;
  display: grid;
  margin: 1em auto;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  padding: 0.4em;
  transform: rotate(var(--rotate));
}
.hightlights__cards-card .arrow img {
  margin: auto;
  width: 15px;
  transform: rotate(315deg);
}
.hightlights__cards-card .humidity {
  max-width: 80%;
  margin: 0.5em auto 0;
}
.hightlights__cards-card .humidity__text {
  justify-content: space-between;
  color: var(--clr-text1);
  font-weight: var(--font-w700);
}
.hightlights__cards-card .humidity__meter {
  --width: 10%;
  position: relative;
  width: 100%;
  height: 8px;
  margin: 0.2em 0;
  background-color: #e6e6ea;
  border-radius: 1em;
}
.hightlights__cards-card .humidity__meter::before {
  position: absolute;
  content: "";
  left: 0;
  height: 100%;
  width: var(--width);
  border-radius: 1em;
  background-color: red;
}
.hightlights__cards-card .humidity > span {
  display: block;
  text-align: right;
  color: var(--clr-text1);
  font-weight: var(--font-w700);
}

.weather__today-searchNav {
  position: fixed;
  top: 0;
  left: -460px;
  width: 100%;
  max-width: 460px;
  height: 100%;
  padding: 2em 1em;
  background-color: var(--clr-bg2);
  transition: transform 0.3s ease-in-out;
}
.weather__today-searchNav.active {
  transform: translateX(460px);
}
.weather__today-searchNav .close-btn {
  display: flex;
  font-size: 1.2em;
  font-weight: var(--font-w700);
  align-items: center;
  justify-content: center;
  margin-left: auto;
  background-color: transparent;
  color: var(--clr-text1);
  border: 0;
  outline: 0;
  border-radius: 50%;
  width: 1.5em;
  aspect-ratio: 1;
  cursor: pointer;
  transition: 0.2s linear;
  margin-bottom: 2em;
}
.weather__today-searchNav .close-btn:hover {
  color: var(--clr-btn-active);
}
.weather__today-searchNav .search__form {
  width: 100%;
  margin-bottom: 2.25em;
}
.weather__today-searchNav .search__form-input {
  padding: 0.7em 1em 0.7em 2em;
  background: inherit;
  border: 1px solid var(--clr-text1);
  outline: 0;
  color: var(--clr-text1);
}
.weather__today-searchNav .search__form-input::placeholder {
  font-weight: var(--font-w500);
  color: var(--clr-text4);
  margin-left: 2em;
}
.weather__today-searchNav .search__form-btn {
  display: block;
  width: 100%;
  font-weight: var(--font-w600);
  color: var(--clr-text1);
  background-color: var(--clr-btn-active);
  border: 0;
  outline: 0;
  cursor: pointer;
}
.weather__today-searchNav .select-site {
  position: relative;
  display: block;
  width: 100%;
  padding: 0.7em 1em;
  background-color: inherit;
  outline: 0;
  color: var(--clr-text4);
  font-weight: var(--font-w500);
  border: 1px solid var(--clr-text1);
  cursor: pointer;
}
.weather__today-searchNav ul {
  width: 100%;
  margin-top: 3em;
  color: var(--clr-text1);
  text-align: left;
  overflow-y: auto;
  height: 330px;
  max-height: 330px;
}
.weather__today-searchNav ul li {
  list-style: none;
  padding: 0.7em 1em;
  cursor: pointer;
  font-weight: var(--font-w500);
  transition: color 0.2s linear;
}
.weather__today-searchNav ul li:hover {
  color: var(--clr-btn-active);
}

.weather__today {
  width: 100%;
  position: relative;
  z-index: 1;
  background-color: var(--clr-bg2);
  min-height: 100vh;
  padding: 2em 1em;
}
.weather__today::before {
  content: "";
  position: absolute;
  inset: 0;
  background: url(../../img/Cloud-background.png);
  background-size: contain;
  background-position: center 15vh;
  background-repeat: no-repeat;
  z-index: -1;
  opacity: 0.3;
}
.weather__today-nav {
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 4.5em;
}
.weather__today-nav button {
  display: block;
  padding: 0.5em 0.8em;
}
.weather__today-nav .location {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.4em;
  background-color: var(--clr-btn-bg);
  border-radius: 50%;
  border: 0;
  outline: 0;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}
.weather__today-nav .location:hover, .weather__today-nav .location:focus-visible {
  background-color: var(--clr-btn-active);
}
.weather__today-nav .search-btn {
  font-size: 1em;
  font-weight: var(--font-w500);
  color: var(--clr-text1);
  background-color: var(--clr-btn-bg);
  border: 0;
  outline: 0;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  transition: background 0.2s linear;
}
.weather__today-nav .search-btn:hover, .weather__today-nav .search-btn:focus-visible {
  background-color: var(--clr-btn-active);
}
.weather__today-img {
  width: max-content;
  margin: 0 auto 2.5em;
  max-width: 47%;
}
.weather__today-temp {
  color: var(--clr-text1);
  padding: 0;
  margin: 0;
  font-size: 9em;
  font-weight: var(--font-w500);
}
.weather__today-temp span {
  color: var(--clr-text2);
  display: inline-block;
  margin-left: -0.7em;
  font-size: 0.3em;
}
.weather__today-name {
  font-size: 2.25em;
  font-weight: var(--font-w600);
  color: var(--clr-text2);
  margin-bottom: 1.5em;
}
.weather__today-date {
  font-size: 1.125em;
  font-weight: var(--font-w500);
  color: var(--clr-text3);
  margin-bottom: 1.5em;
}
.weather__today-date > span {
  margin-left: 0.2em;
}
.weather__today-location {
  font-size: 1.125em;
  font-weight: var(--font-w600);
  color: var(--clr-text3);
}
.weather__today-location svg {
  margin-right: 0.2em;
  margin-bottom: -0.1em;
}
@media only screen and (min-width: 48rem) {
  .weather__today {
    max-width: 460px;
  }
}

.weather__days {
  width: 100%;
  padding: 2em 0;
  max-width: 981px;
}
.weather__days-buttons {
  display: none;
}
.weather__days-buttons button {
  font-size: 1.125em;
  font-weight: var(--font-w700);
  color: var(--clr-text1);
  background-color: var(--clr-bg3);
  border-radius: 50%;
  display: block;
  padding: 0.4em;
  border: 0;
  outline: 0;
  cursor: pointer;
  transition: all 0.3s linear;
}
.weather__days-buttons button:hover, .weather__days-buttons button:focus-visible {
  background-color: var(--clr-btn-active);
}
.weather__days-buttons button.active {
  background-color: var(--clr-text1);
  color: #120f3e;
}
@media only screen and (min-width: 62rem) {
  .weather__days-buttons {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 1em;
    margin-bottom: 3em;
    margin-right: 4.5em;
  }
}
.weather__days-to5 {
  grid-template-columns: repeat(auto-fit, 120px);
  justify-content: center;
  align-items: center;
}
.weather__days-hightlights {
  margin-top: 5em;
}
.weather__days-hightlights h2 {
  font-size: 1.5em;
  font-weight: var(--font-w700);
  color: var(--clr-text1);
  text-align: left;
  margin-left: 3vw;
}
@media only screen and (min-width: 62rem) {
  .weather__days-hightlights h2 {
    margin-left: 7vw;
  }
}
.weather__days-hightlights__cards {
  gap: 2em;
  margin-top: 3em;
  margin-bottom: 3em;
  grid-template-columns: repeat(auto-fit, 328px);
  align-items: center;
  justify-content: center;
}

.weather {
  justify-content: center;
  background-color: var(--clr-bg1);
  min-height: 100vh;
  text-align: center;
}
.weather.flex {
  flex-direction: column;
}
@media only screen and (min-width: 48rem) {
  .weather.flex {
    flex-direction: row;
  }
}